import React from "react"
import ReactDOM from "react-dom"

interface ComponentContainer {

    component: any;

    selector:string;

}

export var components:ComponentContainer[] = [];

export function registerComponent (component:any, selector:string) {

    console.log(component, selector);

    components.push({
        component: component,
        selector: selector
    });

}

export function autobind () {

    components.forEach(component => ReactDOM.render(React.createElement(component.component, null), document.querySelector(component.selector)));

    // components.forEach(component => ReactDOM.render(React.createElement(component.component, null), document.querySelector(component.selector)));

}
